import React, { useState, useContext, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import api from "../api/Axios";
import { AuthContext } from "../context/AuthContext";
import ReactQuill from "react-quill-new";
import "quill/dist/quill.snow.css";
import { modules } from "../module/QuillModules";
import "./CreatePost.css";

function CreatePost() {
  const location = useLocation();
  const category = location.state?.category || "default";
  const [title, setTitle] = useState(localStorage.getItem("postTitle") || "");
  const [content, setContent] = useState(localStorage.getItem("postContent") || "");
  const [file, setFile] = useState(null);
  const { isLoggedIn, userInfo } = useContext(AuthContext);
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (!isLoggedIn) {
      alert("   .");
      navigate("/login");
    }
  }, [isLoggedIn, navigate]);

  useEffect(() => {
    localStorage.setItem("postTitle", title);
    localStorage.setItem("postContent", content);
  }, [title, content]);

  const handleTitleChange = (e) => setTitle(e.target.value);
  const handleContentChange = (value) => setContent(value);
  const handleFileChange = (e) => setFile(e.target.files[0]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const accessToken = localStorage.getItem("accessToken");
      if (!accessToken) {
        alert(" .");
        navigate("/login");
        return;
      }

      const formData = new FormData();
      formData.append("title", title);
      formData.append("content", content);
      formData.append("author", userInfo.username);
      formData.append("category", category);
      if (file) {
        formData.append("file", file);
      }

      const response = await api.post("/posts", formData, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          "Content-Type": "multipart/form-data",
        },
      });

      if (response.status === 201) {
        alert("  !");
        localStorage.removeItem("postTitle");
        localStorage.removeItem("postContent");
        navigate("/");
      }
    } catch (error) {
      console.error("    :", error);
      alert("  .");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div style={{ maxWidth: "600px", margin: "50px auto" }}>
      <h2> </h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder=""
          value={title}
          onChange={handleTitleChange}
          required
          style={{ width: "100%", padding: "10px", margin: "10px 0" }}
        />
        <ReactQuill
          value={content}
          onChange={handleContentChange}
          modules={modules}
          placeholder=" ..."
          className="react-quill-container"
        />
        <input
          type="file"
          onChange={handleFileChange}
          style={{ margin: "10px 0" }}
        />
        <button
          type="submit"
          disabled={isLoading}
          style={{
            width: "100%",
            padding: "10px",
            margin: "10px 0",
            backgroundColor: isLoading ? "#cccccc" : "#007bff",
            color: "#fff",
            border: "none",
            borderRadius: "5px",
            cursor: isLoading ? "not-allowed" : "pointer",
          }}
        >
          {isLoading ? "  ..." : " "}
        </button>
      </form>
    </div>
  );
}

export default CreatePost;
